.buttons{
    display: flex;
    flex-wrap: wrap;
}

@media(max-width : 767px) {
    .buttons {
        flex-basis: 100%;
        margin : 10px;
    }
}

.buttons_button{
    background-color: white;
    border: none;
    color: teal;
    margin: 0 0 0 20px;
    transition: color 0.3s;
}

@media(max-width:767px){
    .buttons_button{
        flex-basis: 100%;
        margin : 10px;
    }
}

.buttons_button:hover{
    color: hsl(180, 100%, 35%);
}

.buttons_button:disabled {
    color: #ccc;
}